      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="clickSearch"></el-button>
        <el-button type="primary" icon="el-icon-refresh" @click="reset"></el-button>
      </el-form-item>
    </el-form>
    <div class="main_bd">
      <el-col class="table_hd">
        <el-button type="primary" icon="el-icon-success" @click="measureConfirm" v-if="isBatchInStored"></el-button>
        <el-button type="primary" icon="el-icon-download" @click="exportExcel" v-if="isExportable" class="pull_right"></el-button>
      </el-col>
      <el-table :data="tableData" ref="table" stripe border style="width:100%" :height="tableHeight" v-loading="loading" @selection-change="handleSelectionChange"> 
        <el-table-column type="selection" width="30" :selectable="setSelectable"></el-table-column>
        <el-table-column prop="furnace" label="" align="center" width="170px" fixed></el-table-column>
