            </div>
            <div v-else>
              <el-input size="mini" v-model="scope.row.ribbonThickness9" @keyup.native="$event => thicknessChangeHandler($event, scope.row)"></el-input>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="ribbonThicknessDeviation" label="(m)" align="center" width="110px"></el-table-column>
        <el-table-column prop="ribbonThickness" label="(m)" align="center" width="110px"></el-table-column>
        <el-table-column prop="ribbonThicknessLevel" label="" align="center" width="90px"></el-table-column>
        <el-table-column prop="ribbonToughness" label="" align="center" width="90px">
          <template slot-scope="scope">
            <div v-if="scope.row.isEditing === false">
