    </el-form>
    <div class="main_bd">
      <el-col class="table_hd">
        <el-button type="primary" icon="el-icon-success" @click="measureConfirm" v-if="userinfo.roleId == 5"></el-button>
      </el-col>
      <el-table :data="tableData" ref="table" stripe border style="width:100%" :height="tableHeight" v-loading="loading" @selection-change="handleSelectionChange"> 
        <el-table-column type="selection" width="30" :selectable="setSelectable"></el-table-column>
        <el-table-column prop="furnace" label="" align="center" width="170px" fixed></el-table-column>
        <el-table-column prop="coilNumber" label="" align="center" width="50px" fixed></el-table-column>
        <el-table-column prop="ribbonTypeName" label="" align="center" width="70px"></el-table-column>
        <el-table-column prop="ribbonWidth" label="" align="center" width="50px"></el-table-column>
        <el-table-column prop="diameter" label="(mm)" align="center" width="90px">
          <template slot-scope="scope">
            <div v-if="scope.row.isEditing === false">
