        </el-table-column>
        <el-table-column prop="laminationFactor" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="laminationLevel" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="realRibbonWidth" label="" align="center" width="80px">
          <template slot-scope="scope">
