            </div>
          </template>
        </el-table-column>
        <el-table-column prop="ribbonThickness9" label="9(m)" align="center" width="90px">
          <template slot-scope="scope">
            <div v-if="scope.row.isEditing === false">
              {{ scope.row.ribbonThickness9 }}
            </div>
            <div v-else>
              <el-input size="mini" v-model="scope.row.ribbonThickness9" @keyup.native="$event => thicknessChangeHandler($event, scope.row)"></el-input>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="ribbonThicknessDeviation" label="(m)" align="center" width="110px"></el-table-column>
        <el-table-column prop="ribbonThickness" label="(m)" align="center" width="110px"></el-table-column>
        <el-table-column prop="ribbonThicknessLevel" label="" align="center" width="90px"></el-table-column>
        <el-table-column prop="ribbonToughness" label="" align="center" width="90px">
          <template slot-scope="scope">
            <div v-if="scope.row.isEditing === false">
              {{ scope.row.ribbonToughness }}
            </div>
            <div v-else>
              <el-select size="mini" v-model="scope.row.ribbonToughness" placeholder="">
                <el-option v-for="item in ribbonToughnessLevelList" :key="item._id" :label="item.ribbonToughness" :value="item.ribbonToughness"></el-option>
              </el-select>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="ribbonToughnessLevel" label="" align="center" width="90px">
        </el-table-column>
        <el-table-column prop="appearence" label="" align="center" width="90px">
          <template slot-scope="scope">
            <div v-if="scope.row.isEditing === false">
