<template>
  <el-dialog 
  :title="dialogData.formType === 'create' ? `-${$route.params.castId}` : `-${$route.params.castId}`" 
  :visible.sync="dialogData.dialogVisible" 
  :close-on-click-modal="false"
  :close-on-press-escape="false" 
  @close="closeDialog"
  :center="true"
  width="1200px"
  v-loading="loading"
  element-loading-text="">
    <el-form :model="formData" :rules="rules" ref="form" label-width="100px" @submit.native.prevent>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="" prop="date" class="dialog_field">
            <el-date-picker v-model="formData.date" type="date" value-format="yyyy-MM-dd" placeholder="" :editable="false" :clearable="false" @change="pick"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="client" class="dialog_field">
            <el-input v-model="formData.client"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="team" class="dialog_field">
            <el-select v-model="formData.team" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="taskOrder" class="dialog_field">
            <el-input v-model="formData.taskOrder"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="" prop="ribbonTypeName" class="dialog_field">
            <el-select v-model="formData.ribbonTypeName" placeholder="">
              <el-option v-for="item in ribbonTypeList" :key="item.ribbonTypeId" :label="item.ribbonTypeName" :value="item.ribbonTypeName"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="ribbonWidth" class="dialog_field">
            <el-select v-model="formData.ribbonWidth" placeholder="">
              <el-option v-for="item in ribbonWidthList" :key="item.ribbonWidthId" :label="item.ribbonWidth" :value="item.ribbonWidth"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col :span="6" v-if="dialogData.formType === 'create'">
          <el-form-item label="" prop="theBeginfurnace" class="dialog_field">
            <el-input v-model="formData.theBeginfurnace"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" v-if="dialogData.formType === 'create'">
          <el-form-item label="" prop="heatNum" class="dialog_field">
            <el-input v-model="formData.heatNum"></el-input>
          </el-form-item>
        </el-col>
        <!--   -->
        <!--  -->
        <el-col :span="6" v-if="dialogData.formType === 'edit'">
          <el-form-item label="" prop="furnace" class="dialog_field">
            <el-input v-model="formData.furnace"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" v-if="dialogData.formType === 'edit'">
          <el-form-item label="" prop="castTime" class="dialog_field">
            <el-input v-model="formData.castTime"></el-input>
          </el-form-item>
        </el-col>
        <!--  -->
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="" prop="alloyWeight" class="dialog_field">
            <el-input v-model="formData.alloyWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="fileNumber" class="dialog_field">
            <el-input v-model="formData.fileNumber"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="remark" class="dialog_field orign_field"> 
            <el-input v-model="formData.remark"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <section class="requirement_detail">
        <h3 class="requirement_hd"></h3>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="(m)" prop="orderThickness" class="dialog_field">
              <el-input v-model="formData.orderThickness"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="orderLaminationFactor" class="dialog_field">
              <el-select v-model="formData.orderLaminationFactor" placeholder="">
                <el-option label="0.75" value="0.75"></el-option>
                <el-option label="0.78" value="0.78"></el-option>
                <el-option label="0.80" value="0.80"></el-option>
                <el-option label="0.82" value="0.82"></el-option>
                <el-option label="0.84" value="0.84"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="orderRibbonToughnessLevels" class="dialog_field">
              <el-select v-model="formData.orderRibbonToughnessLevels" placeholder="" multiple collapse-tags>
                <el-option label="A" value="A"></el-option>
                <el-option label="B" value="B"></el-option>
                <el-option label="C" value="C"></el-option>
                <el-option label="D" value="D"></el-option>
                <el-option label="E" value="E"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="orderAppearenceLevels" class="dialog_field">
              <el-select v-model="formData.orderAppearenceLevels" placeholder="" multiple collapse-tags>
                <el-option label="A" value="A"></el-option>
                <el-option label="B" value="B"></el-option>
                <el-option label="C" value="C"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </section>
      <section class="requirement_detail">
        <h3 class="requirement_hd"></h3>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="(m)" prop="qualifiedThickness" class="dialog_field">
              <el-input v-model="formData.qualifiedThickness"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="qualifiedLaminationFactor" class="dialog_field">
              <el-select v-model="formData.qualifiedLaminationFactor" placeholder="">
                <el-option label="0.75" value="0.75"></el-option>
                <el-option label="0.78" value="0.78"></el-option>
                <el-option label="0.80" value="0.80"></el-option>
                <el-option label="0.82" value="0.82"></el-option>
                <el-option label="0.84" value="0.84"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="qualifiedRibbonToughnessLevels" class="dialog_field">
              <el-select v-model="formData.qualifiedRibbonToughnessLevels" placeholder="" multiple collapse-tags>
                <el-option label="A" value="A"></el-option>
                <el-option label="B" value="B"></el-option>
                <el-option label="C" value="C"></el-option>
                <el-option label="D" value="D"></el-option>
                <el-option label="E" value="E"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="qualifiedAppearenceLevels" class="dialog_field">
              <el-select v-model="formData.qualifiedAppearenceLevels" placeholder="" multiple collapse-tags>
                <el-option label="A" value="A"></el-option>
                <el-option label="B" value="B"></el-option>
                <el-option label="C" value="C"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </section>
    </el-form>
    <div slot="footer">
      <el-button @click="closeDialog"></el-button>
      <el-button type="primary" @click="submitForm"></el-button>
    </div>
  </el-dialog>  
</template>

<script>
import { positiveInteger, ltNumber } from '@/utils/validate';
import urlmap from '@/utils/urlmap';
import { mapState, mapActions } from 'vuex';
import { cloneDeep } from 'lodash';

const formConfig = {
  date: '',
  castId: null,
  team: '',
  taskOrder: '',
  ribbonTypeName: '',
  ribbonWidth: null,
  client: '',
  furnace: '',
  theBeginfurnace: '',
  heatNum: null,
  alloyWeight: null,
  castTime: '',
  rawWeight: null,
  remark: '12',
  fileNumber: '',
  orderThickness: '', // 
  orderLaminationFactor: '', // 
  orderRibbonToughnessLevels: [], // 
  orderAppearenceLevels: [], // 
  qualifiedThickness: '', // 
  qualifiedLaminationFactor: '', // 
  qualifiedRibbonToughnessLevels: [], // 
  qualifiedAppearenceLevels: [], // 
};

export default {
  props: {
    dialogData: {
      type: Object,
      required: true
    }
  },
  data() {
    const checkFurnance = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }

      const reg = /^0[1-9]-[0-9]{8}-[0-9]{2}$/;
      if (reg.test(value)) {
        callback();
      } else {
        callback(new Error(''));
      }
    };

    return {
      roleId: null,
      visible: false,
      loading: false,
      formData: {
        date: '',
        castId: null,
        team: '',
        taskOrder: '',
        ribbonTypeName: '',
        ribbonWidth: null,
        client: '',
        furnace: '',
        theBeginfurnace: '',
        heatNum: null,
        alloyWeight: null,
        castTime: '',
        rawWeight: null,
        remark: '',
        fileNumber: '',
        orderThickness: '', // 
        orderLaminationFactor: '', // 
        orderRibbonToughnessLevels: [], // 
        orderAppearenceLevels: [], // 
        qualifiedThickness: '', // 
        qualifiedLaminationFactor: '', // 
        qualifiedRibbonToughnessLevels: [], // 
        qualifiedAppearenceLevels: [], // 
      },
      rules: {
        date: [{ required: true, message: '', trigger: 'blur' }],
        castId: [{ required: true, message: '', trigger: 'blur' }],
        team: [{ required: true, message: '', trigger: 'blur' }],
        ribbonTypeName: [{ required: true, message: '', trigger: 'blur' }],
        ribbonWidth: [
          { required: true, message: '', trigger: 'blur' },
          { validator: positiveInteger, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        client: [{ max: 50, message: '50', trigger: 'blur' }],
        furnace: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' },
          { validator: checkFurnance, trigger: 'blur'}
        ],
        theBeginfurnace: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' },
          { validator: checkFurnance, trigger: 'blur'}
        ],
        heatNum: [
          { required: true, message: '', trigger: 'blur' },
          { validator: positiveInteger, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }   
        ],
        alloyWeight: [
          { required: true, message: '', trigger: 'blur' },
          { validator: positiveInteger, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }   
        ],
        castTime: [{ max: 50, message: '50', trigger: 'blur' }],
        remark: [{ max: 100, message: '100', trigger: 'blur' }],
        fileNumber: [{ max: 50, message: '50', trigger: 'blur' }],
        orderThickness: [
          { required: true, message: '', trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' },
          { pattern: /^([1-3]\d-[1-3]\d|[1-3]\d)$/, message: '20-2323', trigger: 'blur' },
        ],
        orderLaminationFactor: [
          { required: true, message: '', trigger: 'blur' }
        ],
        orderRibbonToughnessLevels: [
          { required: true, message: '', trigger: 'blur' }
        ],
        orderAppearenceLevels: [
          { required: true, message: '', trigger: 'blur' }
        ],
        qualifiedThickness: [
          { required: true, message: '', trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' },
          { pattern: /^([1-3]\d-[1-3]\d|[1-3]\d)$/, message: '20-2323', trigger: 'blur' },
        ],
        qualifiedLaminationFactor: [
          { required: true, message: '', trigger: 'blur' }
        ],
        qualifiedRibbonToughnessLevels: [
          { required: true, message: '', trigger: 'blur' }
        ],
        qualifiedAppearenceLevels: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapState([
      'ribbonTypeList', 'ribbonWidthList'
    ])
  },
  created () {
    if (this.dialogData.formType === 'create') {
      this.formData = Object.assign({}, formConfig, { castId: Number(this.$route.params.castId) });
    } else {
      this.formData = Object.assign(this.formData, this.dialogData.rowData);
    }
    this.roleId = JSON.parse(localStorage.getItem('userinfo')).roleId;
    this.getRibbonTypeList();
    this.getRibbonWidthList();
  },
  methods: {
    pick() {
      let date = this.formData.date.replace(/-/g, '');
      this.formData.theBeginfurnace = `0${this.formData.castId}-${date}-01`;
    },
    ...mapActions([
      'getRibbonTypeList', 'getRibbonWidthList'
    ]),
    closeDialog() {
      this.$emit('close');
    },
    submitForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true;
          let method = '';
          let url = '';
          let params = null;

          if (this.dialogData.formType === 'create') { // 
            method = 'post';
            url = urlmap.addPlan;
            let formData = [];
            let _heatNum = this.formData.heatNum;
            let fHead = this.formData.theBeginfurnace.substring(0, 12);
            let fEnd = this.formData.theBeginfurnace.substring(12, 14);
            while(_heatNum > 0) {
              _heatNum--;
              let clone = cloneDeep(this.formData);
              clone.furnace = fHead + fEnd;
              clone.castTime = this.setCastTime(fEnd);
              clone.roleId = this.roleId;
              //
              clone.orderRibbonToughnessLevels = clone.orderRibbonToughnessLevels.join();
              clone.orderAppearenceLevels = clone.orderAppearenceLevels.join();
              clone.qualifiedRibbonToughnessLevels = clone.qualifiedRibbonToughnessLevels.join();
              clone.qualifiedAppearenceLevels = clone.qualifiedAppearenceLevels.join();
              formData.push(clone);
              fEnd = (Number(fEnd) + 1) < 10 ? '0' + (Number(fEnd) + 1) : (Number(fEnd) + 1);
            }
            
            params = { formDataJson: JSON.stringify(formData) };
          } else { // 
            method = 'put';
            url = urlmap.updatePlan;
            const clone = cloneDeep(this.formData);
            clone.roleId = this.roleId;
            // this.formData.orderRibbonToughnessLevelsJson = JSON.stringify(this.formData.orderRibbonToughnessLevels);
            // this.formData.orderAppearenceLevelsJson = JSON.stringify(this.formData.orderAppearenceLevels);
            // this.formData.qualifiedRibbonToughnessLevelsJson = JSON.stringify(this.formData.qualifiedRibbonToughnessLevels);
            // this.formData.qualifiedAppearenceLevelsJson = JSON.stringify(this.formData.qualifiedAppearenceLevels);

            clone.orderRibbonToughnessLevels = clone.orderRibbonToughnessLevels.join();
            clone.orderAppearenceLevels = clone.orderAppearenceLevels.join();
            clone.qualifiedRibbonToughnessLevels = clone.qualifiedRibbonToughnessLevels.join();
            clone.qualifiedAppearenceLevels = clone.qualifiedAppearenceLevels.join();
            params = clone;
          }

          this.$http(method, url, params).then(data => {
            this.$emit('submit');
          }).catch(err => {
            console.log(err);
          }).finally(() => {
            this.loading = false;
          });    
        } else {
          return false;
        }
      })
    },
    setCastTime(fEnd) {
      let time = '';
      switch (Number(fEnd)) {
        case 1:
          time = '08:00-10:00';
          break;
        case 2:
          time = '10:00-12:00';
          break;
        case 3:
          time = '12:00-14:00';
          break;
        case 4:
          time = '14:00-16:00';
          break;
        case 5:
          time = '16:00-18:00';
          break;
        case 6:
          time = '18:00-20:00';
          break;
        case 7:
          time = '20:00-22:00';
          break;
        case 8:
          time = '22:00-24:00';
          break;
        case 9:
          time = '00:00-02:00';
          break;
        case 10:
          time = '02:00-04:00';
          break;
        case 11:
          time = '04:00-06:00';
          break;
        case 12:
          time = '06:00-08:00';
          break;
        default:
          break;
      }
      return time;
    }
  }
}
</script>

<style lang="scss" scoped>
.requirement_detail {
  border-top: 1px solid #dcdfe6;
  position: relative;
  padding: 20px 0 10px;
  .requirement_hd {
    position: absolute;
    left: 50%;
    top: -10px;
    transform: translateX(-50%);
    background-color: #fff;
    padding: 0 10px;
  }
}
</style>





