<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="crumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{castId}}</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="search_bar" :model="searchForm" :inline="true">
      <el-form-item label="">
        <el-date-picker v-model="searchForm.date" type="date" value-format="yyyy-MM-dd" placeholder="" :editable="false" :clearable="false" :picker-options="pickerOptions"></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="clickSearch"></el-button>
      </el-form-item>
    </el-form>
    <div class="main_bd">
      <el-col class="table_hd">
        <el-button type="primary" icon="el-icon-plus" @click="createPlan" v-if="roleId === 1 || roleId === 2"></el-button>
        <el-button type="primary" icon="el-icon-check" @click="approve" v-if="(roleId === 1 || roleId === 2) && tableData.length > 0 && !isApproved" :disabled="roleId === 2 && tableData.length > 0 && !isApproved"></el-button>
        <el-button type="primary" icon="el-icon-check" v-if="(roleId === 1 || roleId === 2) && tableData.length > 0 && isApproved" disabled></el-button>
        <el-button type="primary" icon="el-icon-download" @click="exportVisible = true" v-if="isExportable" class="pull_right"></el-button>
      </el-col>
      <el-table :data=tableData stripe border style="width: 100%" v-loading="loading" ref="table" :height="tableHeight">
        <el-table-column prop="date" label="" align="center" width="110px"></el-table-column>
        <el-table-column prop="castId" label="" align="center" width="60px"></el-table-column>
        <el-table-column prop="team" label="" align="center" width="50px"></el-table-column>
        <el-table-column prop="ribbonTypeName" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="ribbonWidth" label="(mm)" align="center" width="90px"></el-table-column>
        <el-table-column prop="furnace" label="" align="center" width="150px"></el-table-column>
        <el-table-column label="">
          <el-table-column label="(m)" align="center" width="90px">
            <template slot-scope="scope">
              <span class="text_danger">{{scope.row.orderThickness}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="80px">
            <template slot-scope="scope">
              <span class="text_danger">{{scope.row.orderLaminationFactor}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="80px">
            <template slot-scope="scope">
              <span class="text_danger">{{scope.row.orderRibbonToughnessLevels.toString()}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="80px">
            <template slot-scope="scope">
              <span class="text_danger">{{scope.row.orderAppearenceLevels.toString()}}</span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" v-if="roleId === 1 || roleId === 2 || roleId === 3">
          <el-table-column label="(m)" align="center" width="90px">
            <template slot-scope="scope">
              <span>{{scope.row.qualifiedThickness}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="80px">
            <template slot-scope="scope">
              <span>{{scope.row.qualifiedLaminationFactor}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="80px">
            <template slot-scope="scope">
              <span>{{scope.row.qualifiedRibbonToughnessLevels.toString()}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="80px">
            <template slot-scope="scope">
              <span>{{scope.row.qualifiedAppearenceLevels.toString()}}</span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column prop="taskOrder" label="" align="center" width="100px"></el-table-column>
        <el-table-column prop="client" label="" align="center" width="100px"></el-table-column>
        <el-table-column prop="alloyWeight" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="castTime" label="" align="center" width="110px"></el-table-column>
        <el-table-column prop="rawWeight" label="" align="center" width="80px"></el-table-column>
        <el-table-column label="(mm)" align="center" width="80px">
          <template slot-scope="scope">
            <span :class="{text_danger: scope.row.realRibbonWidth !== scope.row.ribbonWidth}">{{scope.row.realRibbonWidth}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="150px">
          <template slot-scope="scope" v-if="roleId === 2">
            <el-button size="mini" type="primary" @click="editPlan(scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="delPlan(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="table_tip">
        <p>1. {{tableData[0] && tableData[0].fileNumber}}</p>
        <p>2. {{tableData[0] && tableData[0].remark}}</p>
      </div>
    </div>
    <dialog-form v-if="dialogVisible" :dialogData="{ formType, dialogVisible, rowData}" @close="closeHandler" @submit="submitHandler"></dialog-form>
    <excel-form v-if="exportVisible" :exportData="{ exportVisible, castId }" @closeExport=" exportVisible = false" @submitExport="exportVisible = false"></excel-form>
  </div>
</template>

<script>
import { dateFormat, dateTimeFormat, debounce } from '@/utils/common';
import urlmap from '@/utils/urlmap';
import dialogForm from './components/dialogForm';
import excelForm from './components/excelForm';

export default {
  name: 'plan',
  components: { dialogForm, excelForm },
  data() {
    return {
      searchForm: {
        date: dateFormat(new Date())
      },
      isApproved: false,
      isExportable: false,
      dialogVisible: false,
      exportVisible: false,
      formType: 'create',
      rowData: {},
      tableData: [],
      loading: true,
      castId: 6,
      roleId: null,
      tableHeight: 200
    }
  },
  // 
  beforeRouteUpdate(to, from, next) {
    this.castId = to.params.castId;
    this.getTableData();
    this.isExportable = this.setExportable();
    next();
  },
  created() {
    this.castId = this.$route.params.castId;
    this.getTableData();
    this.roleId = JSON.parse(localStorage.getItem('userinfo')).roleId;
    //  roleId 
    this.isExportable = this.setExportable();
  },
  mounted () {
    const self = this;
    self.$nextTick(() => {
      self.tableHeight = window.innerHeight - self.$refs.table.$el.getBoundingClientRect().top;
    });
    window.onresize = debounce(() => {
      self.tableHeight = window.innerHeight - self.$refs.table.$el.getBoundingClientRect().top;
    }, 1000)
  },
  computed: {
    pickerOptions() {
      return {
        disabledDate(time) {
          let date = new Date();
          date.setDate(date.getDate() + 1);
          return time.getTime() >= date.getTime();
        }
      }
    }
  },
  methods: {
    closeHandler() {
      this.dialogVisible = false;
    },
    submitHandler(data) {
      if (this.formType === 'edit') {
        this.dialogVisible = false;
      }
      this.getTableData();
    },
    clickSearch() {
      this.getTableData();
    },
    setExportable() {
      if ([1, 2, 3].includes(this.roleId)) {
        return true;
      } else {
        return false;
      }
    },
    getTableData() {
      const params = {
        castId: this.castId,
        date: this.searchForm.date
      };
      this.$http('get', urlmap.queryPlan, params).then(data => {
        let _list = data.list;
        _list.forEach(row => {
          row.orderRibbonToughnessLevels = row.orderRibbonToughnessLevels.split(',');
          row.orderAppearenceLevels = row.orderAppearenceLevels.split(',');
          row.qualifiedRibbonToughnessLevels = row.qualifiedRibbonToughnessLevels.split(',');
          row.qualifiedAppearenceLevels = row.qualifiedAppearenceLevels.split(',');
        });
        //  approved === 1 
        this.isApproved = _list.every(item => item.approved === 1);

        // 
        if (!this.isApproved) {
          if (this.roleId === 1 || this.roleId === 2) {
            this.tableData = _list;            
          } else {
            this.tableData = [];
          }
        } else { // 
          this.tableData = _list;
        }
      }).catch((err) => {
        console.log(err);
      }).finally(() => {
        this.loading = false;
      });
    },
    createPlan() {
      this.formType = 'create';
      this.dialogVisible = true;
    },
    editPlan(row) {
      this.dialogVisible = true;
      this.rowData = row;
      this.formType = 'edit';
    },
    delPlan(row) {
      const { planId, furnace } = row;
      this.$confirm(` ${furnace} `, '', { confirmButtonText: '', cancelButtonText: '', type: 'warning'})
      .then(() => {
        this.$http('delete', urlmap.delPlan, {planId}).then(data => {
          this.getTableData();
        }).catch(err => {
          console.log(err);
        });
      })
      .catch(() => {});
    },
    approve() {
      this.$confirm('', '', { confirmButtonText: '', cancelButtonText: '', type: 'warning'})
      .then(() => {
        const params = {
          date: this.searchForm.date,
          castId: this.castId,
          roleId: this.roleId
        };
        this.$http('PUT', urlmap.updatePlan, params).then(data => {
          this.getTableData();
        }).catch(err => {
          console.log(err);
        });
      })
      .catch(() => {});
    }
  }
}
</script>

<style lang="scss" scoped>
  
</style>




