<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="crumb">
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="main_bd">
      <el-col class="table_hd">
        <el-button type="primary" icon="el-icon-plus" @click="createUser"></el-button>
      </el-col>
      <el-table :data="tableData" stripe border style="width:100%" v-loading="loading">
        <el-table-column type="index" label="" align="center" width="100"></el-table-column>
        <el-table-column prop="username" label="" align="center"></el-table-column>
        <el-table-column prop="password" label="" align="center"></el-table-column>
        <el-table-column prop="roleId" label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.roleId === 1"></span>
            <span v-if="scope.row.roleId === 2"></span>
            <span v-if="scope.row.roleId === 3"></span>
            <span v-if="scope.row.roleId === 4"></span>
            <span v-if="scope.row.roleId === 5"></span>
            <span v-if="scope.row.roleId === 6"></span>
            <span v-if="scope.row.roleId === 7">6-</span>
            <span v-if="scope.row.roleId === 8">6-</span>
            <span v-if="scope.row.roleId === 9">7-</span>
            <span v-if="scope.row.roleId === 10">7-</span>
            <span v-if="scope.row.roleId === 11">8-</span>
            <span v-if="scope.row.roleId === 12">8-</span>
            <span v-if="scope.row.roleId === 13">9-</span>
            <span v-if="scope.row.roleId === 14">9-</span>
          </template>
        </el-table-column>
        <el-table-column prop="adminname" label="" align="center"></el-table-column>
        <el-table-column prop="createTime" label="" align="center" width="170px"></el-table-column>
        <el-table-column prop="loginTime" label="" align="center" width="170px"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="delUser(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <dialog-form
      v-if="dialogVisible"
      :dialogData="{ formType, dialogVisible, rowData}"
      @close="closeHandler"
      @submit="submitHandler"
    ></dialog-form>
  </div>
</template>

<script>
import dialogForm from "./components/dialogForm";
import urlmap from "@/utils/urlmap";

export default {
  name: "user",
  components: {
    dialogForm
  },
  data() {
    return {
      dialogVisible: false,
      formType: "create",
      rowData: {},
      tableData: [],
      loading: true
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    getUserList() {
      this.$http("GET", urlmap.queryUser, { roleId: 1 })
        .then(data => {
          this.tableData = data.list;
        })
        .catch(err => {
          console.log(err);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    createUser() {
      this.dialogVisible = true;
      this.formType = "create";
    },
    delUser(row) {
      const { username, adminname } = row;
      this.$confirm(` ${adminname}-${username} `, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http("delete", urlmap.delUser, { username })
            .then(data => {
              this.getUserList();
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(() => {});
    },
    closeHandler() {
      this.dialogVisible = false;
    },
    submitHandler() {
      this.dialogVisible = false;
      this.getUserList();
    }
  }
};
</script>

<style lang="scss" scoped>
</style>





