<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="crumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{castId}}</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="search_bar" :model="searchForm" :inline="true">
      <el-form-item label="">
        <el-date-picker
          v-model="searchForm.date"
          type="daterange"
          :default-time="['00:00:00', '23:59:59']"
          :clearable="false"
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.team" placeholder="">
          <el-option :label="castId + '#-'" :value="castId + '#-'"></el-option>
          <el-option :label="castId + '#-'" :value="castId + '#-'"></el-option>
          <el-option :label="castId + '#-'" :value="castId + '#-'"></el-option>
          <el-option :label="castId + '#-'" :value="castId + '#-'"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.caster" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.furnace" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.ribbonTypeName" placeholder="">
          <el-option v-for="item in ribbonTypeList" :key="item.ribbonTypeId" :value="item.ribbonTypeName" :label="item.ribbonTypeName"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.ribbonWidths" placeholder="" multiple collapse-tags>
          <el-option v-for="item in ribbonWidthList" :key="item.ribbonWidthId" :label="item.ribbonWidth" :value="item.ribbonWidth"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="clickSearch"></el-button>
        <el-button type="primary" icon="el-icon-refresh" @click="reset"></el-button>
      </el-form-item>
    </el-form>
    <div class="main_bd">
      <el-col class="table_hd">
        <el-button type="primary" icon="el-icon-plus" @click="add" v-if="isAddAble"></el-button>
      </el-col>
      <el-table :data="tableData" ref="table"  stripe border style="width:100%" :height="tableHeight" v-loading="loading">
        <el-table-column type="expand" label="" width="50px">
          <template slot-scope="props">
            <!--  -->
            <el-table
            :data="props.row.record" stripe border style="width:100%">
              <el-table-column label="" type="index" width="50px"></el-table-column>
              <el-table-column prop="nozzleSize" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="treatCoolRoller" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="coolRollerThickness" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="ReceiveMeltTime" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="tundishTemperatureWithoutMelt" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="tundishTemperatureWithMelt" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="installNozzleTime" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="castTimeStart" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="pressure" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="tundishTemperatureCasting" label="/" align="center" width="100px"></el-table-column>
              <el-table-column prop="coolRollerTemperatureBeforeCast" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="coolRollerTemperatureAfterCast" label="/" align="center" width="120px"></el-table-column>
              <el-table-column prop="castLocation" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="coilTimes" label="" align="center" width="80px"></el-table-column>
              <el-table-column prop="castTimeEnd" label="" align="center" width="110px"></el-table-column>
              <el-table-column prop="describe" label="" align="center" width="110px" :show-overflow-tooltip="true"></el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <!--   castTimes-->
        <el-table-column prop="createTime" label="" align="center" width="110px" :formatter="dateFormat">
        </el-table-column>
        <el-table-column prop="ribbonTypeName" label="" align="center" width="80px">
        </el-table-column>
        <el-table-column prop="ribbonWidth" label="" align="center" width="50px"></el-table-column>
        <el-table-column prop="furnace" label="" align="center" width="170px"></el-table-column>
        <el-table-column prop="team" label="" align="center" width="70px"></el-table-column>
        <el-table-column prop="caster" label="" align="center" width="70px"></el-table-column>
        <el-table-column prop="castTimes" label="" align="center" width="80px">
          <template slot-scope="scope">
            <span :class="scope.row.castTimes > 1 ? 'text_danger' : '' ">{{scope.row.castTimes}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="tundish" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="tundishCar" label="" align="center" width="80px"></el-table-column>
        <el-table-column prop="isChangeTundish" label="" align="center" width="80px">
          <template slot-scope="scope">
            <span v-if="scope.row.isChangeTundish === 1"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="nozzleNum" label="" align="center" width="100px"></el-table-column>
        <el-table-column prop="heatCupNum" label="" align="center" width="100px"></el-table-column>
        <el-table-column prop="meltOutWeight" label="(kg)" align="center" width="110px"></el-table-column>
        <el-table-column prop="rawWeight" label="(kg)" align="center" width="110px"></el-table-column>
        <el-table-column prop="uselessRibbonWeight" label="(kg)" align="center" width="110px"></el-table-column>
        <el-table-column prop="remark" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="updatedAt" label="" align="center" width="170px" :formatter="dateTimeFormat"></el-table-column>
        <el-table-column prop="updatePerson" label="" align="center" width="70px"></el-table-column>
        <el-table-column label="" align="center" width="150px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="edit(scope.row)" :disabled="userinfo.roleId !== 1 && userinfo.roleId !== 3 && userinfo.adminname !== scope.row.caster"></el-button>
            <el-button size="mini" type="danger" @click="del(scope.row)" v-if="userinfo.roleId === 1 || userinfo.roleId === 3"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="total,prev,pager,next"
        :total="pageConfig.total"
        :current-page.sync="pageConfig.current"
        :page-size="pageConfig.pageSize"
        @current-change="handleCurrentChange"></el-pagination>
    </div>
    <dialog-form v-if="dialogVisible" :dialogData="{ formType, dialogVisible, rowData, ribbonTypeList, ribbonWidthList }" @close="closeHandler" @submit="submitHandler"></dialog-form>
  </div>
</template>

<script>
import urlmap from '@/utils/urlmap';
import { dateFormat, dateTimeFormat, debounce } from '@/utils/common';
import dialogForm from './components/dialogForm.vue';
import { mapState, mapActions } from 'vuex';

export default {
  name: 'cast',
  components: {
    dialogForm
  },
  data () {
    return {
      userinfo: {},
      castId: 6,
      searchForm: {
        caster: '',
        team: '',
        furnace: '',
        ribbonTypeName: '',
        date: [],
        ribbonWidths: []
      },
      loading: false,
      tableData: [],
      dialogVisible: false,
      formType: 'create',
      rowData: {},
      pageConfig: {
        total: 1,
        current: 1,
        pageSize: 10
      },
      isAddAble: false,
      isDelAble: false,
      tableHeight: 500
    }
  },
  computed: {
    ...mapState([
      'ribbonTypeList', 'ribbonWidthList'
    ])
  },
  // 
  beforeRouteUpdate(to, from, next) {
    this.castId = to.params.castId;
    this.getTableData();
    // 
    this.isAddAble = this.setIsAddAble(); 
    this.isDelAble = this.setIsDelAble();
    next();
  },
  created () {
    this.castId = this.$route.params.castId;
    this.userinfo = JSON.parse(localStorage.getItem('userinfo'));
    // 
    this.isAddAble = this.setIsAddAble(); 
    this.isDelAble = this.setIsDelAble();
    this.getTableData();
    this.getRibbonTypeList();
    this.getRibbonWidthList();
  },
  mounted () {
    const self = this;
    self.$nextTick(() => {
      self.tableHeight = window.innerHeight - self.$refs.table.$el.getBoundingClientRect().top;
    });
    window.onresize = debounce(() => {
      self.tableHeight = window.innerHeight - self.$refs.table.$el.getBoundingClientRect().top;
    }, 1000)
  },
  methods: {
    ...mapActions([
      'getRibbonTypeList', 'getRibbonWidthList'
    ]),
    dateFormat(row, column) {
      return dateFormat(row.createTime);
    },
    dateTimeFormat(row, column) {
      return dateTimeFormat(row.updatedAt);
    },
    setIsAddAble() {
      // 76#97#118#139#
      if (this.userinfo.roleId == 7 || this.userinfo.roleId == 9 || this.userinfo.roleId == 11 || this.userinfo.roleId == 13) {
        return true;
      } else { // 
        return false;
      }
    },
    setIsDelAble() {
      if (this.userinfo.roleId === 1 && this.userinfo.roleId === 3) {
        return true;
      }
    },
    clickSearch() {
      // 1
      const params = {
        current: 1
      };
      this.pageConfig.current = 1;
      this.getTableData(params);
    },
    reset() {
      this.searchForm = { caster: '', team: '', furnace: '', ribbonTypeName: '', date: [], ribbonWidths: [] };
      const params = {
        current: 1
      };
      this.pageConfig.current = 1;
      this.getTableData(params);
    },
    getTableData(params = {}) {
      const _params = {
        castId: this.castId,
        team: this.searchForm.team,
        startTime: this.searchForm.date[0],
        endTime: this.searchForm.date[1],
        caster: this.searchForm.caster,
        furnace: this.searchForm.furnace,
        ribbonTypeName: this.searchForm.ribbonTypeName,
        ribbonWidthJson: JSON.stringify(this.searchForm.ribbonWidths)
      };
      Object.assign(params, _params);
      this.$http('get', urlmap.queryCast, params).then(data => {
        this.pageConfig.total = data.count;
        //  record 
        data.list.forEach(item => {
          item.record = JSON.parse(item.record);
        });
        this.tableData = data.list;
      }).catch((err) => {
        console.log(err);
      }).finally(() => {
        this.loading = false;
      });
    },
    add() {
      this.dialogVisible = true;
      this.formType = 'add';
    },
    edit(row) {
      this.rowData = row;
      this.dialogVisible = true;
      this.formType = 'edit';
    },
    del(row) {
      const { _id, furnace } = row;
      this.$confirm(` ${furnace} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http('delete', urlmap.delCast, {_id}).then(data => {
          this.getTableData();
        }).catch(error => {
          console.log(error);
        });
      }).catch(() => {});
    },
    closeHandler() {
      this.dialogVisible = false;
    },
    submitHandler() {
      if (this.formType === 'edit') {
        this.dialogVisible = false;
      }
      this.pageConfig.current = 1;
      this.getTableData();
    },
    handleCurrentChange(val) {
      const params = {
        current: val
      };
      this.getTableData(params);
    }
  }
}
</script>




