<template>
  <el-dialog
  :title="dialogData.formType === 'create' ? `-${$route.params.castId}` : `-${$route.params.castId}`"
  :visible.sync="dialogData.dialogVisible"
  :close-on-click-modal="false"
  :close-on-press-escape="false" 
  @close="closeDialog"
  :center="true"
  width="950px"
  v-loading="loading"
  element-loading-text="">
    <el-form :model="formData" :rules="rules" ref="form" label-width="100px" @submit.native.prevent inline>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label=":" prop="ribbonTypeName" class="dialog_field">
            <el-select v-model="formData.ribbonTypeName" placeholder="">
              <el-option v-for="item in dialogData.ribbonTypeList" :key="item.ribbonTypeName" :value="item.ribbonTypeName" :label="item.ribbonTypeName"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="furnace" class="dialog_field">
            <el-input v-model="formData.furnace" @blur="setBucket"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="bucket" class="dialog_field">
            <span>{{formData.bucket}}</span>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label=":" prop="melter" class="dialog_field">
            <span>{{formData.melter}}</span>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="meltFurnace" class="dialog_field">
            <el-select v-model="formData.meltFurnace">
              <el-option label="A" value="A"></el-option>
              <el-option label="B" value="B"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="newAlloyNumber" class="dialog_field">
            <el-input v-model="formData.newAlloyNumber"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label=":" prop="newAlloyWeight" class="dialog_field">
            <el-input v-model="formData.newAlloyWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="oldAlloyNumber" class="dialog_field">
            <el-input v-model="formData.oldAlloyNumber"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="oldAlloyWeight" class="dialog_field">
            <el-input v-model="formData.oldAlloyWeight"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label=":" prop="mixAlloyNumber" class="dialog_field">
            <el-input v-model="formData.mixAlloyNumber"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="mixAlloyWeight" class="dialog_field">
            <el-input v-model="formData.mixAlloyWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="highNbNumber" class="dialog_field">
            <el-input v-model="formData.highNbNumber"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label=":" prop="highNbWeight" class="dialog_field">
            <el-input v-model="formData.highNbWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="Si" class="dialog_field">
            <el-input v-model="formData.Si"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="Ni" class="dialog_field">
            <el-input v-model="formData.Ni"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label=":" prop="Cu" class="dialog_field">
            <el-input v-model="formData.Cu"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="BFe" class="dialog_field">
            <el-input v-model="formData.BFe"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="NbFe" class="dialog_field">
            <el-input v-model="formData.NbFe"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label=":" prop="alloyOutWeight" class="dialog_field">
            <el-input v-model="formData.alloyOutWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="alloyFixWeight" class="dialog_field">
            <el-input v-model="formData.alloyFixWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label=":" prop="remark" class="dialog_field">
            <el-input v-model="formData.remark"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer">
      <el-button @click="closeDialog"></el-button>
      <el-button type="primary" @click="submitForm"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import { integer, positiveInteger, ltNumber, checkFurnace, number } from '@/utils/validate';
import urlmap from '@/utils/urlmap';

const formConfig = {
  "date": "", //
  "castId": 6,// 
  "furnace": "",//   06-20181120-01/01
  "ribbonTypeName": "", //
  "bucket": '',// 
  "melter": "",// 
  "meltFurnace": "",//  A  B
  "newAlloyNumber": "",//  B-2018-12-12
  "newAlloyWeight": 0,// 
  "oldAlloyNumber": "",//  B-2018-12-12
  "oldAlloyWeight": 0,//
  "mixAlloyNumber": "",//  B-2018-12-12
  "mixAlloyWeight": 0,// 
  "highNbNumber": "",//  B-2018-12-12
  "highNbWeight": 0,// 
  "Si": 0,//
  "Ni": 0,// 
  "Cu": 0,//
  "BFe": 0,//
  "NbFe": 0,//
  "alloyTotalWeight": 0,//
  "alloyOutWeight": 0,//
  "alloyFixWeight": 0,// 
  "remark":  "",
  "createdAt": "", //
  "updatedAt": "" //
};

export default {
  data() {
    return {
      userinfo: {},
      visible: false,
      loading: false,
      formData: {
        "date": "", //
        "castId": 6,// 
        "furnace": "",//   06-20181120-01/01
        "ribbonTypeName": "", //
        "bucket": '',// 
        "melter": '',// 
        "meltFurnace": "",//  A  B
        "newAlloyNumber": "",//  B-2018-12-12
        "newAlloyWeight": 0,// 
        "oldAlloyNumber": "",//  B-2018-12-12
        "oldAlloyWeight": 0,//
        "mixAlloyNumber": "",//  B-2018-12-12
        "mixAlloyWeight": 0,// 
        "highNbNumber": "",//  B-2018-12-12
        "highNbWeight": 0,// 
        "Si": 0,//
        "Ni": 0,// 
        "Cu": 0,//
        "BFe": 0,//
        "NbFe": 0,//
        "alloyTotalWeight": 0,//
        "alloyOutWeight": 0,//
        "alloyFixWeight": 0,// 
        "remark":  "",
        "createdAt": "", //
        "updatedAt": "" //
      },
      rules: {
        ribbonTypeName: [{ required: true, message: '', trigger: 'blur' }],
        furnace: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' },
          { validator: checkFurnace, trigger: 'blur'}
        ],
        bucket: [
          { required: true, message: '', trigger: 'blur' },
          { max: 2, message: '2', trigger: 'blur' }
        ],
        melter: [
          { required: true, message: '', trigger: 'blur' },
          { max: 10, message: '10', trigger: 'blur' }
        ],
        meltFurnace: [{ required: true, message: '', trigger: 'blur' }],
        newAlloyNumber: [
          { max: 20, message: '20', trigger: 'blur' }
        ],
        newAlloyWeight: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        oldAlloyNumber: [
          { max: 20, message: '20', trigger: 'blur' }
        ],
        oldAlloyWeight: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        mixAlloyNumber: [
          { max: 20, message: '20', trigger: 'blur' }
        ],
        mixAlloyWeight: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        highNbNumber: [
          { max: 20, message: '20', trigger: 'blur' }
        ],
        highNbWeight: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        Si: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        Ni: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        Cu: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        BFe: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        NbFe: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        alloyOutWeight: [
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        alloyFixWeight: [
          { validator: integer, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        remark: [
          { max: 100, message: '100', trigger: 'blur' }
        ]
      }
    };
  },
  props: {
    dialogData: {
      type: Object,
      required: true
    }
  },
  created() {
    this.userinfo = JSON.parse(localStorage.getItem('userinfo'));
    if (this.dialogData.formType === 'create') {
      this.formData = Object.assign({}, formConfig, {castId: Number(this.$route.params.castId), melter: this.userinfo.adminname});
    } else {
      this.formData = Object.assign(this.formData, this.dialogData.rowData);
    }
  },
  methods: {
    setBucket(e) { // 
      const furnace = e.target.value;
      if(furnace) {
        this.formData.bucket = e.target.value.split('/')[1];
      }
    },
    closeDialog() {
      this.$emit('close');
    },
    submitForm() {
      this.$refs.form.validate((valid) => {
        if(valid) {
          this.loading = true;

          // 
          this.formData.alloyTotalWeight = parseInt(this.formData.newAlloyWeight) + parseInt(this.formData.oldAlloyWeight) + parseInt(this.formData.mixAlloyWeight) + parseInt(this.formData.highNbWeight) + parseInt(this.formData.alloyFixWeight);

          this.formData.roleId = this.userinfo.roleId;
          this.formData.adminname = this.userinfo.adminname;

          const { method, url } = this.dialogData.formType === 'create' ? { method: 'POST', url: urlmap.addMelt } : { method: 'PUT', url: urlmap.updateMelt };

          this.$http(method, url, this.formData).then(data => {
            this.$emit('submit');
          }).catch((error) => {
            console.log(error);
          }).finally(() => {
            this.loading = false;
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>



