<template>
  <el-dialog
  :title="dialogData.formType === 'add' ? `-${$route.params.castId}` : `-${$route.params.castId}`"
  :visible.sync="dialogData.dialogVisible"
  :close-on-click-modal="false"
  :close-on-press-escape="false" 
  @close="closeDialog"
  :center="true"
  width="1200px"
  v-loading="loading"
  element-loading-text="">
    <el-form :model="formData" :rules="rules" ref="form" label-width="100px" @submit.native.prevent>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="" prop="ribbonTypeName" class="dialog_field">
            <el-select v-model="formData.ribbonTypeName" placeholder="">
              <el-option v-for="item in dialogData.ribbonTypeList" :key="item.ribbonTypeId" :value="item.ribbonTypeName" :label="item.ribbonTypeName"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="ribbonWidth" class="dialog_field">
            <el-select v-model="formData.ribbonWidth" placeholder="">
              <el-option v-for="item in dialogData.ribbonWidthList" :key="item.ribbonWidthId" :label="item.ribbonWidth" :value="item.ribbonWidth"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="furnace" class="dialog_field">
            <el-input v-model="formData.furnace"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label=":" prop="caster" class="dialog_field">
            <span>{{formData.caster}}</span>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="" prop="tundish" class="dialog_field">
            <el-input v-model="formData.tundish" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="tundishCar" class="dialog_field">
            <el-select v-model="formData.tundishCar">
              <el-option label="#1" value="1"></el-option>
              <el-option label="#2" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="isChangeTundish" class="dialog_field">
            <el-select v-model="formData.isChangeTundish">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label=":" prop="nozzleNum" class="dialog_field">
            <span>{{formData.nozzleNum}}</span>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="" prop="heatCupNum" class="dialog_field">
            <el-input v-model="formData.heatCupNum"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="meltOutWeight" class="dialog_field">
            <el-input v-model="formData.meltOutWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="rawWeight" class="dialog_field">
            <el-input v-model="formData.rawWeight"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="uselessRibbonWeight" class="dialog_field">
            <el-input v-model="formData.uselessRibbonWeight"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="" prop="team">
            <el-select v-model="formData.team">
              <el-option :label="formData.castId + '#-'" :value="formData.castId + '#-'"></el-option>
              <el-option :label="formData.castId + '#-'" :value="formData.castId + '#-'"></el-option>
              <el-option :label="formData.castId + '#-'" :value="formData.castId + '#-'"></el-option>
              <el-option :label="formData.castId + '#-'" :value="formData.castId + '#-'"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="castTimes">
            <el-select v-model="formData.castTimes" @change="castTimesChangeHandler">
              <el-option label="0" :value="0"></el-option>
              <el-option label="1" :value="1"></el-option>
              <el-option label="2" :value="2"></el-option>
              <el-option label="3" :value="3"></el-option>
              <el-option label="4" :value="4"></el-option>
              <el-option label="5" :value="5"></el-option>
              <el-option label="6" :value="6"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="remark" class="dialog_field">
            <el-input v-model="formData.remark"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <section class="cast_detail" v-for="(item, index) in formData.record" :key="index">
        <h3 class="cast_hd">{{index+1}}</h3>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.nozzleSize'" :rules="[{ required: true, message: ' 30*0.25', trigger: 'blur' }, { max: 10, message: '10', trigger: 'blur' }]">
              <el-input v-model="item.nozzleSize" placeholder="30*0.25"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.treatCoolRoller'" :rules="[{ required: true, message: '', trigger: 'blur' }]">
              <el-select v-model="item.treatCoolRoller" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.coolRollerThickness'" :rules="[{ required: true, message: ' mm', trigger: 'blur' }, { pattern: /^(0|[1-9]\d{0,8})$/, message: '9,  0,1,2,3,...', trigger: 'blur' }]">
              <el-input v-model="item.coolRollerThickness"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.ReceiveMeltTime'" :rules="[{ required: true, message: ' 08:30', trigger: 'blur' },{ max: 10, message: '10', trigger: 'blur' }]">
              <el-input v-model="item.ReceiveMeltTime" placeholder="08:30"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.tundishTemperatureWithoutMelt'" :rules="[{ required: true, message: '', trigger: 'blur' }, { pattern: /^[1-9]\d{0,8}$/, message: '9,  1350,...', trigger: 'blur' }]">
              <el-input v-model="item.tundishTemperatureWithoutMelt"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.tundishTemperatureWithMelt'" :rules="[{ required: true, message: '', trigger: 'blur' }, { pattern: /^[1-9]\d{0,8}$/, message: '9,  1350,...', trigger: 'blur' }]">
              <el-input v-model="item.tundishTemperatureWithMelt"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.installNozzleTime'" :rules="[{ required: true, message: ' 08:40', trigger: 'blur' },{ max: 10, message: '10', trigger: 'blur' }]">
              <el-input v-model="item.installNozzleTime" placeholder="08:40"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.castTimeStart'" :rules="[{ required: true, message: ' 08:40', trigger: 'blur' },{ max: 10, message: '10', trigger: 'blur' }]">
              <el-input v-model="item.castTimeStart" placeholder="09:20"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.pressure'" :rules="[{ required: true, message: '', trigger: 'blur' }, { pattern: /^[1-9]\d{0,8}$/, message: '9,  18,...', trigger: 'blur' }]">
              <el-input v-model="item.pressure"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.tundishTemperatureCasting'" :rules="[{ required: true, message: '', trigger: 'blur' }, { pattern: /^[1-9]\d{0,8}$/, message: '9,  1350,...', trigger: 'blur' }]">
              <el-input v-model="item.tundishTemperatureCasting"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.coolRollerTemperatureBeforeCast'" :rules="[{ required: true, message: '', trigger: 'blur' }, { pattern: /^(\d+|\d+\.\d{1,2})-(\d+|\d+\.\d{1,2})$/, message: ',  32.50-32.50', trigger: 'blur' }]">
              <el-input v-model="item.coolRollerTemperatureBeforeCast" placeholder="32-32"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="" class="dialog_field" :prop="'record.' + index + '.coolRollerTemperatureAfterCast'" :rules="[{ required: true, message: '', trigger: 'blur' }, { pattern: /^(\d+|\d+\.\d{1,2})-(\d+|\d+\.\d{1,2})$/, message: ',  32.50-33.80', trigger: 'blur' }]">
              <el-input v-model="item.coolRollerTemperatureAfterCast" placeholder="32-35"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.castLocation'" :rules="[{ required: true, message: '', trigger: 'blur' }]">
              <el-select v-model="item.castLocation" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.coilTimes'" :rules="[{ required: true, message: '', trigger: 'blur' }]">
              <el-select v-model="item.coilTimes">
                <el-option label="0" value="0"></el-option>
                <el-option label="1" value="1"></el-option>
                <el-option label="2" value="2"></el-option>
                <el-option label="3" value="3"></el-option>
                <el-option label=">3" value=">3"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.castTimeEnd'" :rules="[{ required: true, message: ' 08:40', trigger: 'blur' },{ max: 10, message: '10', trigger: 'blur' }]">
              <el-input v-model="item.castTimeEnd" placeholder="10:30"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" class="dialog_field" :prop="'record.' + index + '.describe'" :rules="[{ required: true, message: '', trigger: 'blur' },{ max: 100, message: '100', trigger: 'blur' }]">
              <el-input v-model="item.describe"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </section>
    </el-form>
    <div slot="footer">
      <el-button @click="closeDialog"></el-button>
      <el-button type="primary" @click="submitForm"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import { integer, positiveInteger, ltNumber, checkFurnace, number } from '@/utils/validate';
import urlmap from '@/utils/urlmap';
import { mapState, mapActions } from 'vuex';
import { cloneDeep } from 'lodash';

const defaultCastDetail = {
  "nozzleSize": "", //
  "nozzleNum": 1, //
  "heatCupNum": 1, //
  "treatCoolRoller": "", // 
  "coolRollerThickness": null, //
  "ReceiveMeltTime": "", //
  "tundishTemperatureWithoutMelt": null, // 
  "tundishTemperatureWithMelt": null, //  
  "installNozzleTime": "", //
  "castTimeStart": "", //
  "pressure": null, //
  "tundishTemperatureCasting": null, // 
  "coolRollerTemperatureBeforeCast": "", //
  "coolRollerTemperatureAfterCast": "", //
  "castLocation": "", //
  "coilTimes": null, //
  "castTimeEnd": "", // 
  "describe": "" //
};

const formConfig = {
  "castTimes": 1, //1
  "castId": 6,// 
  "team": '', // 
  "furnace": "",//   06-20181120-01/01
  "caster": '', //
  "ribbonWidth": null, //
  "ribbonTypeId": '',//id
  "ribbonTypeName": "", //
  "nozzleNum": 1, //
  "heatCupNum": 0, //
  "tundishCar": '', //
  "tundish": '', //
  "isChangeTundish": '', // 1- 0-
  "meltOutWeight": 0, //
  "rawWeight": null, //
  "remark": '', //
  "createdAt": "", //
  "updatedAt": "", //
  "uselessRibbonWeight": null, // 
  record: [
    defaultCastDetail
  ]
};

export default {
  data() {
    return {
      userinfo: {},
      visible: false,
      loading: false,
      formData: {
        "castTimes": 1, //1
        "castId": 6,// 
        "team": '', //
        "furnace": "",//   06-20181120-01/01
        "caster": '', //
        "ribbonWidth": null, //
        "ribbonTypeId": '',//id
        "ribbonTypeName": "", //
        "nozzleNum": 1, //
        "heatCupNum": 1, //
        "tundishCar": '', //
        "tundish": '', //
        "isChangeTundish": '', // 1- 0-
        "meltOutWeight": 0, //
        "rawWeight": null, //
        "remark": '', //
        "createdAt": "", //
        "updatedAt": "", //
        "uselessRibbonWeight": null, // 
        record: [
          {
            "nozzleSize": "", //
            "nozzleNum": 1, //
            "heatCupNum": 1, //
            "treatCoolRoller": "", // 
            "coolRollerThickness": null, //
            "ReceiveMeltTime": "", //
            "tundishTemperatureWithoutMelt": null, // 
            "tundishTemperatureWithMelt": null, //  
            "installNozzleTime": "", //
            "castTimeStart": "", //
            "pressure": null, //
            "tundishTemperatureCasting": null, // 
            "coolRollerTemperatureBeforeCast": "", //
            "coolRollerTemperatureAfterCast": "", //
            "castLocation": "", //
            "coilTimes": null, //
            "castTimeEnd": "", // 
            "describe": "" //
          }
        ]
      },
      rules: {
        ribbonTypeName: [{ required: true, message: '', trigger: 'blur' }],
        ribbonWidth: [
          { required: true, message: '', trigger: 'blur' },
          { validator: positiveInteger, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        furnace: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' },
          { validator: checkFurnace, trigger: 'blur'}
        ],
        caster: [
          { required: true, message: '', trigger: 'blur' },
          { max: 10, message: '10', trigger: 'blur' }
        ],
        tundish: [
          { required: true, message: '', trigger: 'blur' },
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        tundishCar: [{ required: true, message: '', trigger: 'blur' }],
        isChangeTundish: [{ required: true, message: '', trigger: 'blur' }],
        nozzleNum: [
          { required: true, message: '', trigger: 'blur' },
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        heatCupNum: [
          { required: true, message: '', trigger: 'blur' },
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(99999), trigger: 'blur' }
        ],
        meltOutWeight: [
          { required: true, message: '', trigger: 'blur' },
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(500), trigger: 'blur' }
        ],
        rawWeight: [
          { required: true, message: '', trigger: 'blur' },
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(500), trigger: 'blur' }
        ],
        remark: [
          { max: 50, message: '50', trigger: 'blur' }
        ],
        castTimes: [{ required: true, message: '', trigger: 'blur' }],
        uselessRibbonWeight: [
          { required: true, message: '', trigger: 'blur' },
          { validator: number, trigger: 'blur' },
          { validator: ltNumber(500), trigger: 'blur' }
        ],
        team: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  props: {
    dialogData: {
      type: Object,
      required: true
    }
  },
  created() {
    this.userinfo = JSON.parse(localStorage.getItem('userinfo'));
    if (this.dialogData.formType === 'add') {
      const _formConfig = cloneDeep(formConfig);
      this.formData = Object.assign({}, _formConfig, {castId: Number(this.$route.params.castId), caster: this.userinfo.adminname});
    } else {
      this.formData = Object.assign(this.formData, this.dialogData.rowData);
      this.formData.castTimes = this.formData.record.length;
    }
  },
  mounted() {},
  methods: {
    closeDialog() {
      this.$emit('close');
    },
    castTimesChangeHandler(val) { // 
      this.formData.nozzleNum = val; // 
      const _length = this.formData.record.length;
      if (val > _length) {
        for(let i = 0; i < val - _length; i++) {
          const clone = cloneDeep(defaultCastDetail);
          this.formData.record.push(clone);
        }
      } else if (val < _length) {
        for(let i = 0; i < _length - val; i++) {
          this.formData.record.pop();
        }
      }
    },
    submitForm() {
      this.$refs.form.validate((valid) => {
        if(valid) {
          this.loading = true;

          this.formData.recordJson = JSON.stringify(this.formData.record);
          this.formData.roleId = this.userinfo.roleId;
          this.formData.adminname = this.userinfo.adminname;

          const { method, url } = this.dialogData.formType === 'add' ? { method: 'POST', url: urlmap.addCast } : { method: 'PUT', url: urlmap.updateCast };

          this.$http(method, url, this.formData).then(data => {
            this.$emit('submit');
          }).catch((error) => {
            console.log(error);
          }).finally(() => {
            this.loading = false;
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/ .el-dialog__body {
  height: 500px;
  overflow: auto;
}
.cast_detail {
  border-top: 1px solid #dcdfe6;
  position: relative;
  padding: 20px 0 10px;
  .cast_hd {
    position: absolute;
    left: 50%;
    top: -10px;
    transform: translateX(-50%);
    background-color: #fff;
    padding: 0 10px;
  }
}
</style>



