// 

// 
export const integer = (rule, value, callback) => {
  const reg = /^(0|-?[1-9]\d*)$/;
  if(!reg.test(value)) {
    callback(new Error(',  -2,-1,0,1,2,...'))
  } else {
    callback();
  }
};

// 
export const positiveInteger = (rule, value, callback) => {
  const reg = /^[1-9]\d*$/;
  if(!reg.test(value)) {
    callback(new Error(',  1,2,3,...'))
  } else {
    callback();
  }
};

//  0, 1, 2, ...
export const number = (rule, value, callback) => {
  const reg = /^(0|[1-9]\d*)$/;
  if(!reg.test(value)) {
    callback(new Error(',  0,1,2,3,...'))
  } else {
    callback();
  }
};

// 2
export const decimalFormat = (rule, value, callback) => {
  const reg = /^(\-?)\d+(?:\.\d{1,2})?$/;
  if(!reg.test(value)) {
    callback(new Error(',  15, 15.7, 15.75,...'))
  } else {
    callback();
  }
};

//  99999999
export const ltNumber = (max = 99999999) => {
  return (rule, value, callback) => {
    if (typeof value !== 'number') {
      value = Number(value);
    }
  
    if(value <= max) {
      callback();      
    } else {
      callback(new Error(`${max}`))
    }
  };
};

//  0
export const gtNumber = (min = 0) => {
  return (rule, value, callback) => {
    if (typeof value !== 'number') {
      value = Number(value);
    }
  
    if(value >= min) {
      callback();
    } else {
      callback(new Error(`${min}`))
    }
  };
};

//  [0, 99999999]
export const Range = (min = 0, max = 99999999) => {
  return (rule, value, callback) => {
    if (typeof value !== 'number') {
      value = Number(value);
    }
  
    if(value >= min && value <= max) {
      callback();      
    } else {
      callback(new Error(`${min} - ${max}`))
    }
  };
};

export const checkFurnace = (rule, value, callback) => {
  if (!value) {
    return callback(new Error(''));
  }

  const reg = /^0[1-9]-[0-9]{8}-[0-9]{2}\/[0-9]{2}$/;
  if (reg.test(value)) {
    callback();
  } else {
    callback(new Error(''));
  }
};


